.flex-prf {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: -60px;
}

.flex-prf-item {
  text-decoration: none;
  display: block;
  width: 50%;
  position: relative;
  border: 1px solid #fff;
  border-top: none;
  border-left: none;
  width: 25%;
}

.flex-prf-item span {
  display: block;
}

.flex-prf-item:hover:before {
  opacity: 1;
}

.flex-prf-item:hover .site-link {
  opacity: 1;
}

.flex-prf-item:hover .center-content {
  opacity: 1;
}

.flex-prf-item:hover .tl {
  width: 100%;
  transition: width .2s ease;
}

.flex-prf-item:hover .tr {
  height: 100%;
  transition: height .2s ease .2s;
}

.flex-prf-item:hover .br {
  width: 100%;
  transition: width .2s ease .4s;
}

.flex-prf-item:hover .bl {
  height: 100%;
  transition: height .2s ease .6s;
}

.flex-prf-item .tl,
.flex-prf-item .tr,
.flex-prf-item .br,
.flex-prf-item .bl {
  position: absolute;
  background-color: #1b6fec;
}

.flex-prf-item .tl {
  top: 0;
  left: 0;
  width: 0;
  height: 15px;
  transition: width .2s ease .6s;
}

.flex-prf-item .tr {
  top: 0;
  right: 0;
  width: 15px;
  height: 0;
  transition: height .2s ease .4s;
}

.flex-prf-item .br {
  width: 0;
  height: 15px;
  bottom: 0;
  right: 0;
  transition: width .2s ease .2s;
}

.flex-prf-item .bl {
  width: 15px;
  height: 0;
  left: 0;
  bottom: 0;
  transition: height .2s ease;
}

.flex-prf-item:before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  background-color: rgba(28, 33, 46, 0.8);
  transition: opacity .6s ease;
}

.flex-prf-item img {
  width: 100%;
  height: auto;
}

.center-content {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  text-align: left;
  padding-left: 45px;
  padding-right: 25px;
  opacity: 0;
  transition: opacity .6s ease;
}

.prf-item-title {
  color: #ffffff;
  font-family: "GothamPro";
