.hover-plan {
  position: relative;
  overflow: hidden;
}

.hover-plan > * {
  position: relative;
  z-index: 1;
}

.hover-plan:before {
  content: '';
  display: block;
  position: absolute;
  background-color: #1b6fec;
  transition: transform .2s ease;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.hover-plan:hover:before {
  transform: translate3d(0%, 0%, 0);
  opacity: 1;
}

.hover-plan:hover.hover-plan_right:before {
  opacity: 1;
  transform: scaleY(1);
}

.hover-plan:hover .price-box__link {
  color: #fff;
  border-bottom-color: #fff;
}

.hover-plan_top:before {
  transform: translate3d(0%, -100%, 0);
}

.hover-plan_bottom:before {
  transform: translate3d(0%, 100%, 0);
}

.hover-plan_right {
  overflow: visible;
}

.hover-plan_right:before {
  transition: transform .2s linear, opacity .2s linear;
  width: 650px;
  opacity: 0;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  transform: scaleY(0);
}

